<template><section><h1>Notification</h1>
<h4>提示栏组件</h4>
<template>
    <coding :code="test1Code" title="notification" content="简单的notify，不带内容，只有标题">
        <y-button type="ghost" @click.native="test1">成功！</y-button>
        <y-button type="ghost" @click.native="test2">危险！</y-button>
        <y-button type="ghost" @click.native="test3">失败！</y-button>
        <y-button type="ghost" @click.native="test4">禁止！</y-button>
    </coding>
    <coding :code="test2Code" title="带icon大notify" content="带有icon和内容">
        <y-button type="ghost" @click.native="test5">成功！</y-button>
        <y-button type="ghost" @click.native="test6">危险！</y-button>
        <y-button type="ghost" @click.native="test7">失败！</y-button>
        <y-button type="ghost" @click.native="test8">禁止！</y-button>
    </coding>
    <coding :code="test3Code" title="自动关闭" content="1秒后自动关闭">
        <y-button type="ghost" @click.native="test9">自动关闭</y-button>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td><code>String</code></td>
<td><code>success</code></td>
<td><code>info</code>,<code>warning</code>,<code>danger</code></td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭时的回调</td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>icon</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>time</td>
<td>延迟关闭时间</td>
<td><code>Number</code></td>
<td><code>0(默认不关闭,毫秒)</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            test1Code:
`this.$notify("成功！")
this.$notify.danger("危险！")
this.$notify.info("信息！")
this.$notify.warning("禁止！")
` ,
            test2Code:
`this.$notify({
    "title":"test",
    "content":"sfafasdf",
    "icon":true
})
this.$notify.success({
    "title":"test",
    "content":"sfafasdf",
    "icon":true
})
this.$notify.info({
    "title":"test",
    "content":"sfafasdf",
    "icon":true
})
this.$notify.warning({
    "title":"test",
    "content":"sfafasdf",
    "icon":true
})
`,
            test3Code:
`this.$notify({
    "title":"我会自动关闭",
    "content":"嘿嘿嘿哈哈哈",
    "icon":true,
    "time": 1000
})
`

        }
    },
    methods:{
        test1(){
            this.$notify("成功！")
        },
        test2(){
            this.$notify.danger("危险！")
        },
        test3(){
            this.$notify.info("信息！")
        },
        test4(){
            this.$notify.warning("禁止！")
        },
        test5(){
            this.$notify({
                "title":"成功！",
                "content":"嘿嘿嘿哈哈哈",
                "icon":true,
            })
        },
        test6(){
            this.$notify.danger({
                "title":"危险！",
                "content":"嘿嘿嘿哈哈哈",
                "icon":true,
            })
        },
        test7(){
            this.$notify.info({
                "title":"信息！",
                "content":"嘿嘿嘿哈哈哈",
                "icon":true,
            })
        },
        test8(){
            this.$notify.warning({
                "title":"禁止！",
                "content":"嘿嘿嘿哈哈哈",
                "icon":true,
            })
        },
        test9(){
            this.$notify({
                "title":"我会自动关闭",
                "content":"嘿嘿嘿哈哈哈",
                "icon":true,
                "time": 1000
            })
        }
    }
}
</script>


